<template>
  <div>
    <div id="header" class="content">
      <header-nav></header-nav>
    </div>
    <div class="content padding-top-100">

      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="封面图">
          <el-upload
            action="#"
            :http-request="handleHttpRequest"
            :show-file-list="false"
            :on-remove="handleRemove"
            class="avatar-uploader">
            <img v-if="form.topicimg!=null" :src="osshost+form.topicimg" class="avatar">
            <i v-else class="el-icon-plus step-upicon">点击上传</i>
          </el-upload>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
          <el-tag type="info">只能上传jpg/png文件，且不超过5mb</el-tag>
        </el-form-item>
        <el-form-item label="主题名称">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="主题简介">
          <el-input type="textarea" v-model="form.introduction"></el-input>
        </el-form-item>
        <el-form-item label="推荐食谱">
          <el-tag type="info">(填入管饭网的菜谱ID来添加菜谱，通过菜谱链接查看ID，如https://www.guanfan.top/cookbook?id=43639填入43639即可)</el-tag>
          <el-form-item
            v-for="(domain, index) in foodList"
            :label="'食谱' + parseInt(index+1)"
            :key="index"
            class="mt20"
          >
            <el-col :span="12">
              <el-input v-model="domain.recipeid"></el-input>
              <el-input type="textarea" v-model="domain.reason"></el-input>
            </el-col>
            <el-col :span="2" class="ml10">
              <el-button type="danger" icon="el-icon-delete" circle plain
                         @click.prevent="delthemeFood(index)"></el-button>
            </el-col>
          </el-form-item>
          <el-form-item class="mt20">
            <el-button @click="addthemeFood()">添加1个食谱</el-button>
          </el-form-item>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="onSubmit">发布主题</el-button>
        </el-form-item>
      </el-form>
    </div>
    <FooterBottom></FooterBottom>
  </div>
</template>

<script>
  import headerNav from "@/components/headerNav";
  import FoodItem from "@/components/foodItem";
  import FooterBottom from "@/components/footer";
  import {uploadImg} from '../api/operate'
  import {pushTheme,getEditTheme,pushEditTheme,delThemeFood} from '../api/theme'
  import {Message} from 'element-ui'
  import tools from '@/utils/tools'

  let OSS = require('ali-oss');
  export default {
    components: {
      headerNav,
      FoodItem,
      FooterBottom
    },
    data() {
      return {
        osshost: process.env.UPLOAD_URL,
        topicid: '',
        type:'',
        form: {
          title: '',
          topicimg: null,
          introduction: '',
          relationrecipe: ''
        },
        foodList: [{
          id: 0,
          recipeid:'',
          sort:100,
          reason: ''
        }]
      }
    },
    created() {
      this.topicid = this.$route.query.id;
      this.type = this.$route.query.type;
      if (this.type == 3) {
        this.getDataInfo(this.topicid);
      }
    },
    mounted() {
    },
    methods: {
      delthemeFood(index) {
        if (index !== -1) {
          if(this.foodList[index].id!==0){
            let id=this.foodList[index].id;
            delThemeFood(this.topicid,id).then(response => {
              let res = response.data;
              if (res.success) {
                Message({
                  message: '关联食谱已删除',
                  type: 'success',
                  duration: 5 * 1000
                })
                this.foodList.splice(index, 1);
              } else {
                Message({
                  message: response.message,
                  type: 'error',
                  duration: 5 * 1000
                })
              }
            }).catch(error => {
              this.$router.push({path: "/login"})
            })
          }else {
            this.foodList.splice(index, 1)
          }
        }
      },
      addthemeFood() {//增加食谱
        let length =this.foodList.length;
        let sort = this.foodList[length-1].sort-1;
        let object = {
          id: 0,
          recipeid:'',
          sort:sort,
          reason: ''
        }
        this.foodList.push(object);
      },
      onSubmit() {
        let string = "";
        for (var i = 0; i < this.foodList.length; i++) {
          if (i == 0) {
            string += this.foodList[i].id+ '#' + this.foodList[i].sort + '#' + this.foodList[i].recipeid + '#' + this.foodList[i].reason;
          } else {
            string += '|' + this.foodList[i].id + '#' + this.foodList[i].sort + '#' + this.foodList[i].recipeid + '#' + this.foodList[i].reason;
          }
        }
        this.form.relationrecipe = string;
        if(this.type == 3){
          this.form.topicid=this.topicid;
          pushEditTheme(this.form).then(response => {
            let res = response.data;
            if (res.success) {
              Message({
                message: '修改成功！',
                type: 'success',
                duration: 5 * 1000
              })
              this.$router.go(-1);//返回上一层
            }
          }).then(error => {
            console.log(error)
          })
        }else{
          pushTheme(this.form).then(response => {
            let res = response.data;
            if (res.success) {
              Message({
                message: '发布成功！',
                type: 'success',
                duration: 5 * 1000
              })
              this.$router.go(-1);//返回上一层
            }
          }).then(error => {
            console.log(error)
          })
        }

      },
      handleRemove() {
        this.form.topicimg = null;
      },
      handleHttpRequest(a) {//上传封面图
        let dialogImageUrl = a.file.name;
        let type = 5;
        uploadImg(type).then(response => {
          let result = response.data.result;
          let imgUrl;
          imgUrl = result.spaceid + '/' + tools.imgMd5(dialogImageUrl, imgUrl, 'topic');//文件名 加密
          const client = new OSS({
            region: 'oss-cn-hangzhou',
            accessKeyId: result.AccessKeyId,
            accessKeySecret: result.AccessKeySecret,
            stsToken: result.SecurityToken,
            bucket: 'guanfan'//请求阿里云之后获得
          })
          client.put(imgUrl, a.file).then(results => {
            this.form.topicimg = results.name;
            // if(results.res.requestUrls[0].indexOf('?') != -1){
            //   this.url = results.res.requestUrls[0].split('?')[0];
            // }else{
            //   //console.log('图片100k以内')
            //   this.url = results.res.requestUrls[0]
            // }
            console.log('头像上传成功');
          }).catch((err) => {
            console.log(err);
          });
        }).catch(error => {
          console.log(error)
        })
      },
      getDataInfo(topicid){
        getEditTheme(topicid).then(response => {
          let res = response.data;
          if (res.success) {
            this.form = res.result;
            this.foodList = this.form.cplist;
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
    }
  };
</script>

<style scoped>
  .step-upicon {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    font-size: 14px;
    color: #8c939d;
    width: 150px;
    height: 150px;
    line-height: 150px;
    text-align: center;
  }

  .step-upicon:hover {
    border-color: #409EFF;
  }
  .avatar {
    width: 380px;
    height: 200px;
    display: block;
  }
</style>



